import React from 'react';
import PropTypes from 'prop-types';
import BlcButtonBase from 'components/BlcButton/BlcButtonBase';
import { color, util } from 'public/styles';

/**
 * Warning style button.
 */

const blcButtonWarning = props => (
	<BlcButtonBase
		id={props.id}
		name={props.name}
		color={props.inverted ? color.warning : color.asphalt}
		colorDisabled={props.inverted ? color.gray : color.grayDark}
		colorFocus={props.inverted ? color.warningDark : color.asphalt}
		colorHover={props.inverted ? color.warningLight : color.asphalt}
		background={props.inverted ? color.transparent : color.warning}
		backgroundDisabled={props.inverted ? color.transparent : color.grayLight}
		backgroundFocus={props.inverted ? color.transparent : color.warningDark}
		backgroundHover={props.inverted ? color.transparent : color.warningLight}
		border={color.warning}
		borderDisabled={props.inverted ? color.gray : color.grayLight}
		borderFocus={props.inverted ? color.warningDark : color.warning}
		borderHover={props.inverted ? color.warningLight : color.warning}
		disabled={props.disabled}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={props.rounded ? util.blcButtonRadius(props.size) : '0'}
		size={util.blcButtonSize(props.size)}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonWarning.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	id: PropTypes.string.isRequired,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	name: PropTypes.string.isRequired,
	rounded: PropTypes.bool,
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest'])
};

blcButtonWarning.defaultProps = {
	children: null,
	disabled: false,
	id: null,
	inverted: false,
	margin: '0',
	name: null,
	rounded: false,
	size: null
};

export default blcButtonWarning;
