import React from 'react';
import PropTypes from 'prop-types';
import BlcButtonBase from 'components/BlcButton/BlcButtonBase';
import { color, util } from 'public/styles';

/**
 * Float branded button.
 */

const blcButtonFloat = props => (
	<BlcButtonBase
		id={props.id}
		name={props.name}
		color={color.white}
		colorDisabled={color.grayDark}
		colorFocus={color.white}
		colorHover={color.white}
		border={props.inverted ? color.asphalt : color.float}
		borderDisabled={props.inverted ? color.gray : color.grayLight}
		borderFocus={props.inverted ? color.asphaltDark : color.floatDark}
		borderHover={props.inverted ? color.asphaltLight : color.floatLight}
		background={props.inverted ? color.asphalt : color.float}
		backgroundDisabled={props.inverted ? color.gray : color.grayLight}
		backgroundFocus={props.inverted ? color.asphaltDark : color.floatDark}
		backgroundHover={props.inverted ? color.asphaltLight : color.floatLight}
		disabled={props.disabled}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={props.rounded ? util.blcButtonRadius(props.size) : '0'}
		size={util.blcButtonSize(props.size)}
	>
		{props.children}
	</BlcButtonBase>
);

blcButtonFloat.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	id: PropTypes.string.isRequired,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	name: PropTypes.string.isRequired,
	rounded: PropTypes.bool,
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest'])
};

blcButtonFloat.defaultProps = {
	children: null,
	disabled: false,
	id: null,
	inverted: false,
	margin: '0',
	name: null,
	rounded: true,
	size: null
};

export default blcButtonFloat;
