import React from 'react';
import PropTypes from 'prop-types';
import BlcButtonBase from 'components/BlcButton/BlcButtonBase';
import { color, util } from 'public/styles';

/**
 * Primary button style.
 */

const blcButton = props => (
	<BlcButtonBase
		id={props.id}
		name={props.name}
		color={props.inverted ? color.white : color.asphalt}
		colorDisabled={color.grayDark}
		colorFocus={props.inverted ? color.white : color.asphalt}
		colorHover={props.inverted ? color.white : color.asphalt}
		border={props.inverted ? color.asphalt : color.backlot}
		borderDisabled={props.inverted ? color.gray : color.grayLight}
		borderFocus={props.inverted ? color.asphaltDark : color.backlotDark}
		borderHover={props.inverted ? color.asphaltLight : color.backlotLight}
		background={props.inverted ? color.asphalt : color.backlot}
		backgroundDisabled={props.inverted ? color.gray : color.grayLight}
		backgroundFocus={props.inverted ? color.asphaltDark : color.backlotDark}
		backgroundHover={props.inverted ? color.asphaltLight : color.backlotLight}
		disabled={props.disabled}
		margin={props.margin}
		padding={util.blcButtonPadding(props.size)}
		rounded={props.rounded ? util.blcButtonRadius(props.size) : '0'}
		size={util.blcButtonSize(props.size)}
	>
		{ props.children }
	</BlcButtonBase>
);

blcButton.propTypes = {
	children: PropTypes.node,
	disabled: PropTypes.bool,
	id: PropTypes.string,
	inverted: PropTypes.bool,
	margin: PropTypes.string,
	name: PropTypes.string,
	rounded: PropTypes.bool,
	size: PropTypes.oneOf([null, 'largest', 'larger', 'smaller', 'smallest'])
};

blcButton.defaultProps = {
	children: null,
	disabled: false,
	id: null,
	inverted: false,
	margin: '0',
	name: null,
	rounded: false,
	size: null
};

export default blcButton;
