import React from 'react';
import PropTypes from 'prop-types';
import { size, weight, color, unit } from 'public/styles';

/*
 * The base component for all BLC buttons. Do NOT use this button,
 * instead look * at the buton components built upon this one.
 */

const blcButtonBase = props => {

	return(
		<button disabled={props.disabled} id={props.id} name={props.name} key={props.id} type={props.type}>
			{props.children}

			<style jsx>{`
			appearance: none;
			background-color: ${props.background};
			border: ${size.border} solid ${props.border ? props.border : (props.background ? props.background : color.backlot)};
			border-radius: ${props.rounded};
			color: ${props.color};
			cursor: pointer;
			font-family: MarkPro;
			font-size: ${props.size};
			font-stretch: normal;
			font-style: normal;
			font-weight: ${weight.bold};
			letter-spacing: normal;
			line-height: 1;
			margin: ${props.margin};
			padding: ${props.padding};
			text-align: center;
			transition: background .15s linear, border .15s linear;
			width: fit-content;

			button:hover {
				background-color: ${props.backgroundHover};
				border-color: ${props.borderHover ? props.borderHover : (props.backgroundHover ? props.backgroundHover : color.backlotLight)};
				color: ${props.colorHover};
			}

			button:active {
				background-color: ${props.backgroundFocus};
				border-color: ${props.borderFocus ? props.borderFocus : (props.backgroundFocus ? props.backgroundFocus : color.backlotDark)};
				color: ${props.colorFocus};
			}

			button:focus {
				outline: 0;
			}

			button:disabled,
			button[disabled] {
				background-color: ${props.backgroundDisabled};
				border-color: ${props.borderDisabled ? props.borderDisabled : (props.backgroundDisabled ? props.backgroundDisabled : color.grayLight)};
				color: ${props.colorDisabled};
				cursor: not-allowed;
			}
		`}</style>
		</button>
	);
}

blcButtonBase.propTypes = {
	background: PropTypes.string,
	backgroundDisabled: PropTypes.string,
	backgroundFocus: PropTypes.string,
	backgroundHover: PropTypes.string,
	border: PropTypes.string,
	borderDisabled: PropTypes.string,
	borderFocus: PropTypes.string,
	borderHover: PropTypes.string,
	children: PropTypes.node,
	color: PropTypes.string,
	colorDisabled: PropTypes.string,
	colorFocus: PropTypes.string,
	colorHover: PropTypes.string,
	disabled: PropTypes.bool,
	margin: PropTypes.string,
	padding: PropTypes.string,
	id: PropTypes.string,
	name: PropTypes.string,
	type: PropTypes.oneOf(['button', 'submit', 'reset']),
	rounded: PropTypes.string,
	size: PropTypes.string
};

blcButtonBase.defaultProps = {
	background: color.backlot,
	backgroundDisabled: color.grayLight,
	backgroundFocus: color.backlotDark,
	backgroundHover: color.backlotLight,
	border: color.backlot,
	borderDisabled: color.grayLight,
	borderFocus: color.backlotDark,
	borderHover: color.backlotLight,
	children: null,
	color: color.asphalt,
	colorDisabled: color.grayDark,
	colorFocus: color.asphalt,
	colorHover: color.asphalt,
	disabled: false,
	margin: size.marginSmallest,
	type: 'submit',
	padding: unit(1.75) + ' ' + size.margin,
	rounded: '0',
	size: size.p
};

export default blcButtonBase;
